:root {
  --primaryDkBlue: hsl(208, 100%, 34%);
  --primaryLtBlue: hsl(200, 89%, 85%);
  --primaryYellow: hsl(49, 100%, 50%);
  --primaryDkYellow: hsl(60, 100%, 38%);
  --primaryDkGreen: hsl(124, 45%, 47%);
  --primaryGreen: hsl(92, 51%, 51%);
  --primaryOrange: hsl(32, 94%, 60%);
  --primaryDkOrange: hsl(23, 73%, 43%);
  --primaryRed: hsl(347, 100%, 45%);
  --weBlue: hsl(196, 100%, 47%);
  --black: hsl(0, 0%, 0%);
  --textGray: hsl(0, 0%, 44%);
  --slateGray: hsl(210, 13%, 50%);
  --dkSlateGray: hsl(180, 25%, 25%);
  --dkGray: hsl(0, 0%, 41%);
  --ltGray: hsl(0, 0%, 83%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif;
  --font-heading: "Georama", Helvetica, Arial, sans-serif: // heading font ;
}

@keyframes no-transform {
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes pulse {
  0% {
    opacity: 1;
    transform: none;
  }
  50% {
    opacity: 0.8;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 300ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 300ms ease-in-out forwards;
  display: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  font-family: var(--font-text);
  line-height: 1.4;
  background-color: var(--white);
  margin: 0;
  padding: 0;
}

.container {
  width: 80%;
  width: min(90%, 75em);
  margin-inline: auto;
}
.container__content {
  width: 60%;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .container__content {
    width: 80%;
  }
}
@media (max-width: 43.74875em) {
  .container__content {
    width: 100%;
  }
}

.row {
  padding: 0 1.25rem;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-jc-c {
  justify-content: center;
}
.flex-jc-fs {
  justify-content: flex-start;
}
.flex-jc-fe {
  justify-content: flex-end;
}
.flex-ai-fs {
  align-items: flex-start;
}
.flex-ai-c {
  align-items: center;
}
.flex-fd-r {
  flex-direction: row;
}
.flex-fd-c {
  flex-direction: column;
}
.flex-fw-w {
  flex-wrap: wrap;
}
.flex-fw-nw {
  flex-wrap: nowrap;
}

@media (max-width: 59.999375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 60em) {
  .hide-for-desktop {
    display: none;
  }
}

h1,
h2,
h3,
h4,
h5 {
  margin-top: 0;
  font-family: var(--font-heading);
  font-weight: 700;
  line-height: 1.1;
}

h1 {
  font-size: rem(75);
}

h2 {
  font-size: rem(58);
}

h3 {
  font-size: rem(44);
}

h4 {
  font-size: rem(34);
}

h5 {
  font-size: rem(28);
}

p {
  font-family: var(--font-text);
  color: var(--textGrey);
  font-size: rem(16);
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: rem(24);
}

.intro {
  font-family: var(--font-text);
  color: var(--textGrey);
  font-size: rem(22);
  font-weight: 700;
}

a,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}

.button {
  text-align: center;
  display: inline-block;
  cursor: pointer;
}

.btn {
  font-family: var(--font-heading);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
  padding: 0.5rem 1rem;
  border: 0px;
  border-radius: 0px;
  cursor: pointer;
  letter-spacing: 0.0625rem;
}
.btn--lc {
  text-transform: lowercase;
  font-weight: 300;
}
.btn--white {
  background-color: var(--white);
  border: 1px solid var(--white);
  color: var(--primaryDkBlue);
  font-weight: 700;
  font-size: 1rem;
}
.btn--white:hover, .btn--white:active, .btn--white:focus {
  background-color: var(--primaryYellow);
  color: var(--primaryDkBlue);
  border: 0.0625rem solid var(--primaryYellow);
  transition: all 300ms ease-in-out;
}
.btn--dark {
  background-color: var(--primaryDkBlue);
  border: 0.0625rem solid var(--primaryDkBlue);
  color: var(--white);
  font-weight: 700;
  font-size: 1rem;
}
.btn--dark:hover, .btn--dark:active, .btn--dark:focus {
  background-color: var(--white);
  color: var(--primaryDkBlue);
  border: 0.0625rem solid var(--primaryDkBlue);
  transition: all 300ms ease-in-out;
}
.btn--dark-footer {
  font-size: 1.5rem;
  font-weight: 500;
  padding: 0.625rem 3.5rem;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
  display: block;
}

header {
  background-color: var(--primaryYellow);
  width: 100%;
  position: fixed;
  z-index: 900;
}
header.open .header__menu > span:first-child {
  transform: rotate(45deg);
}
header.open .header__menu > span:nth-child(2) {
  opacity: 0;
}
header.open .header__menu > span:last-child {
  transform: rotate(-45deg);
}
header .overlay {
  opacity: 0;
  position: fixed;
  top: 3.75rem;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--primaryYellow);
  z-index: 400;
  display: none;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 400;
}
@media (max-width: 59.999375em) {
  .nav {
    padding: 1rem 0;
  }
}
.nav__list {
  display: flex;
  align-items: center;
  list-style-type: none;
}
@media (max-width: 59.999375em) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  padding-left: 0;
  transition: all 300ms ease-in-out;
}
.nav__toggle {
  width: 2.25em;
  margin-right: 0.625em;
  position: relative;
  cursor: pointer;
}
.nav__toggle > span {
  display: block;
  width: 1.625em;
  height: 0.125em;
  background-color: var(--primaryDkBlue);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.nav__toggle > span:not(:last-child) {
  margin-bottom: 0.3125em;
}
.nav__item a {
  color: var(--primaryDkBlue);
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.0625em;
  cursor: pointer;
  position: relative;
  margin-left: 1.875em;
  transition: color 0.2s ease-out;
}
@media (max-width: 59.999375em) {
  .nav__item a {
    margin-left: 0px;
  }
}
.nav__item a::after {
  border-radius: 0.0625em;
  border-top: 0.1875em solid var(--white);
  content: "";
  position: absolute;
  right: 100%;
  bottom: -em(8);
  left: 0;
  transition: right 0.4s cubic-bezier(0, 0.5, 0, 1), border-color 0.4s ease-out;
}
.nav__item a:hover {
  color: var(--white);
  text-decoration: none;
}
.nav__item a:hover::after {
  animation: anchor-underline 2s cubic-bezier(0, 0.5, 0, 1) infinite;
  border-color: var(--white);
}
.nav__item--lc {
  font-weight: 400;
  text-transform: none;
}
.nav__item--btn {
  margin-left: 2.1875rem;
}
@media (max-width: 59.999375em) {
  .nav__item--btn {
    margin-left: 0px;
  }
}

.nav-open {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 4.6875em;
  text-align: center;
  width: 90%;
  padding-top: 1.25em;
  padding-left: 0;
  padding-bottom: 1.25em;
  z-index: 400;
}

.nav__item,
.nav__item--btn {
  padding-top: 0.625em;
  padding-bottom: 0.625em;
}

.chevron-down {
  font-size: 1.125rem;
  font-weight: 500;
}